<template>
  <div id="anime-index-container">
    <div class="py-2 d-flex justify-content-between">
      <!-- = Start = Filter = -->
      <div class="py-2 d-flex justify-content-start flex-grow-1">
        <a-input-search
          v-model:value="filter.data.name"
          placeholder="搜索動漫名稱..."
          style="width: 200px"
          @search="getAnimeData()"
        />
        <a-button
          type="primary"
          class="h-100 d-flex align-items-center"
          @click="toggleFilterDrawer()"
        >
          <filter-outlined />
          進階篩選
        </a-button>
        <a-button
          class="h-100 d-flex align-items-center"
          @click="clearFilter()"
          danger
        >
          <sync-outlined />
          清空
        </a-button>
        <a-button class="h-100 d-flex align-items-center" @click="test()">
          TEST
        </a-button>
        <a-drawer v-model:visible="filter.drawer.visible" placement="left">
          <a-form layout="vertical">
            <a-row :gutter="24">
              <a-col :span="24">
                <a-form-item label="名稱">
                  <a-input
                    v-model:value="filter.data.name"
                    placeholder="搜索動漫名稱..."
                  />
                </a-form-item>
              </a-col>
            </a-row>
            <a-row :gutter="24">
              <a-col :span="12">
                <a-form-item label="年份">
                  <a-select
                    show-search
                    v-model:value="filter.data.year"
                    :options="filter.options.year"
                  ></a-select>
                </a-form-item>
              </a-col>
              <a-col :span="12">
                <a-form-item label="季度">
                  <a-select
                    show-searchx
                    v-model:value="filter.data.season"
                    :options="filter.options.season"
                  >
                  </a-select>
                </a-form-item>
              </a-col>
            </a-row>
          </a-form>
          <template #footer>
            <div class="d-flex justify-content-end">
              <a-button
                type="primary"
                class="mr-2"
                @click="
                  getAnimeData();
                  filter.drawer.visible = false;
                "
                >更新</a-button
              >
            </div>
          </template>
        </a-drawer>
      </div>
      <!-- = End = Filter = -->
      <!-- = Start = Display type = -->
      <div class="py-2 d-flex justify-content-end flex-grow-1">
        <a-radio-group v-model:value="options.display.type" :size="'large'">
          <a-radio-button value="row">
            <bars-outlined />
          </a-radio-button>
          <a-radio-button value="block">
            <appstore-outlined />
          </a-radio-button>
        </a-radio-group>
      </div>
      <!-- = End = Display type = -->
    </div>
    <div class="pb-4">
      篩選器：
      <a-tag v-show="filter.data.name.length"
        >名稱：{{ filter.data.name }}</a-tag
      >
      <a-tag v-show="filter.data.year.length"
        >年份：{{ filter.data.year }}</a-tag
      >
      <a-tag v-show="filter.data.season.length">
        季度：{{
          filter.options.season.find((item) => {
            return item.value === filter.data.season;
          }).label
        }}
      </a-tag>
    </div>
    <!-- = Start = Anime List = -->
    <div v-if="options.display.type == 'row'">
      <a-table
        :columns="anime.columns"
        :dataSource="anime.data"
        :pagination="false"
        row-class-name="anime-table-row"
        :loading="loading.anime"
      >
        <template #bodyCell="{ column, record }">
          <template v-if="column.key == 'cover'">
            <a-image
              class="anime-table-cover-image"
              :src="record.coverImage"
            ></a-image>
          </template>
          <template v-else-if="column.key == 'title'">
            <div class="w-100">
              <router-link to="" class="text-reset">
                {{ record.title }}
              </router-link>
            </div>
          </template>
          <!-- = Start = Anime Action = -->
          <template v-else-if="column.key == 'action'">
            <template v-if="is_authenticated">
              <div class="d-flex gap-1 justify-content-end">
                <div
                  class="anime-action-completed"
                  v-if="record.action.can_complete"
                >
                  <a-tooltip title="看完啦~" color="lime" :mouseLeaveDelay="0">
                    <check-circle-filled
                      @click="
                        updateAnimeData({
                          mediaId: record.id,
                          status: 'COMPLETED',
                        })
                      "
                    />
                  </a-tooltip>
                </div>
                <div
                  class="anime-action-current"
                  v-if="record.action.can_watch"
                >
                  <a-tooltip
                    title="開始觀看"
                    color="green"
                    :mouseLeaveDelay="0"
                  >
                    <eye-filled
                      @click="
                        updateAnimeData({
                          mediaId: record.id,
                          status: 'CURRENT',
                        })
                      "
                    />
                  </a-tooltip>
                </div>
                <div class="anime-action-add" v-if="record.action.can_add">
                  <a-tooltip
                    title="又看了一集"
                    color="green"
                    :mouseLeaveDelay="0"
                  >
                    <plus-circle-filled
                      @click="
                        updateAnimeData({
                          mediaId: record.id,
                          progress: record.progression.episodes + 1,
                        })
                      "
                    />
                  </a-tooltip>
                </div>
                <div
                  class="anime-action-planning"
                  v-if="record.action.can_planning"
                >
                  <a-tooltip
                    title="之後會看"
                    color="purple"
                    :mouseLeaveDelay="0"
                  >
                    <calendar-filled
                      @click="
                        updateAnimeData({
                          mediaId: record.id,
                          status: 'PLANNING',
                        })
                      "
                    />
                  </a-tooltip>
                </div>
                <div class="anime-action-delete" v-if="record.progression">
                  <a-tooltip
                    title="不管這個了!"
                    color="red"
                    :mouseLeaveDelay="0"
                  >
                    <close-circle-filled
                      @click="
                        deleteAnimeData({
                          id: record.progression.id,
                          mediaId: record.id,
                        })
                      "
                    />
                  </a-tooltip>
                </div>
              </div>
            </template>
          </template>
          <!-- = End = Anime Action = -->
          <template v-else-if="column.key == 'status'">
            <div
              class="
                d-flex
                justify-content-center
                align-items-center
                border-left
              "
            >
              <template v-if="record.progression_status == 'NONE'"
                >&nbsp;</template
              >
              <template v-else-if="record.progression_status == 'COMPLETED'">
                <a-tooltip title="看完啦~" color="lime">
                  <check-circle-two-tone
                    style="font-size: 32px"
                    two-tone-color="#a0d911"
                  />
                </a-tooltip>
              </template>
              <template v-else-if="record.progression_status == 'CURRENT'">
                <a-tooltip :title="'看到第 ' + record.progression.episodes + ' 集'" color="green">
                  <play-circle-two-tone
                    style="font-size: 32px"
                    two-tone-color="#52c41a"
                  />
                </a-tooltip>
              </template>
              <template v-else-if="record.progression_status == 'PLANNING'">
                <a-tooltip title="以後會看" color="purple">
                  <calendar-two-tone
                    style="font-size: 32px"
                    two-tone-color="#722ed1"
                  />
                </a-tooltip>
              </template>
            </div>
          </template>
        </template>
      </a-table>
    </div>
    <div v-if="options.display.type == 'block'">
      <div class="w-100" v-show="loading.anime">
        <div
          class="w-100 py-2 d-flex justify-content-center align-items-center"
        >
          <a-spin size="large" />
        </div>
      </div>
      <a-row
        v-for="row_index in Math.ceil(
          anime.data.length / options.display.block.amount_in_row
        )"
        :key="row_index - 1"
        :gutter="28"
        class="my-2"
        v-show="!loading.anime"
      >
        <a-col
          :span="parseInt(24 / options.display.block.amount_in_row)"
          v-for="col_index in options.display.block.amount_in_row"
          :key="col_index - 1"
        >
          <div
            class="d-flex justify-content-center align-items-center flex-column"
          >
            <div
              class="
                d-flex
                justify-content-center
                align-items-center
                anime-block-cover-box
              "
            >
              <a-image
                class="anime-block-cover-image"
                :src="
                  anime.data[
                    (row_index - 1) * options.display.block.amount_in_row +
                      (col_index - 1)
                  ].coverImage
                "
              ></a-image>
            </div>
            <router-link to="#" class="mt-2 mb-4">
              <a-typography-paragraph
                :ellipsis="{ rows: 2 }"
                strong
                :content="
                  anime.data[
                    (row_index - 1) * options.display.block.amount_in_row +
                      (col_index - 1)
                  ].title
                "
              >
              </a-typography-paragraph>
            </router-link>
          </div>
        </a-col>
      </a-row>
    </div>
    <!-- = End = Anime List = -->
    <!-- = Start = Pagination = -->
    <div class="pagination align-items-center justify-content-center mt-3">
      <a-pagination
        v-model:current="anime.pagination.current"
        v-model:pageSize="anime.pagination.per_page"
        :total="anime.pagination.total"
        simple
        :disabled="loading.anime"
        @change="getAnimeData()"
      />
    </div>
    <!-- = End = Pagination = -->
  </div>
</template>

<script>
import {
  AppstoreOutlined,
  BarsOutlined,
  CalendarFilled,
  CalendarTwoTone,
  CheckCircleTwoTone,
  CheckCircleFilled,
  CloseCircleFilled,
  EyeFilled,
  FilterOutlined,
  PlayCircleTwoTone,
  PlusCircleFilled,
  SyncOutlined,
} from "@ant-design/icons-vue";

const currentTime = new Date();

export default {
  name: "Anime Index",
  components: {
    AppstoreOutlined,
    BarsOutlined,
    CalendarFilled,
    CalendarTwoTone,
    CheckCircleTwoTone,
    CheckCircleFilled,
    CloseCircleFilled,
    EyeFilled,
    FilterOutlined,
    PlayCircleTwoTone,
    PlusCircleFilled,
    SyncOutlined,
  },
  data () {
    return {
      is_authenticated: this.$store.state.auth.is_authenticated,
      options: {
        display: {
          type: "row",
          block: {
            amount_in_row: 4,
          }
        }
      },
      filter: {
        data: {
          name: '',
          year: '',
          season: '',
        },
        options: {
          year: Array.from({ length: currentTime.getFullYear() - 1938 }, (_, y) => {
            return {
              value: `${y + 1940}`,
              label: `${y + 1940}`
            }
          }).concat({
            value: '',
            label: '不指定'
          }).reverse(),
          season: [
            {
              value: '',
              label: '不指定',
            },
            {
              value: 'WINTER',
              label: '冬季',
            },
            {
              value: 'SPRING',
              label: '春季',
            },
            {
              value: 'SUMMER',
              label: '夏季',
            },
            {
              value: 'FALL',
              label: '秋季',
            },
          ],
        },
        drawer: {
          visible: false,
        },
      },
      loading: {
        anime: true,
      },
      anime: {
        columns: [
          {
            title: "封面",
            key: 'cover',
            align: 'center',
            width: 70,
          },
          {
            title: "標題",
            dataIndex: "title",
            key: "title",
          },
          {
            title: "操作",
            key: 'action',
            width: 140,
          },
          {
            title: "狀態",
            key: 'status',
            width: 60,
          },
        ],
        data: [],
        pagination: {
          per_page: 50,
          total: 1,
          current: 1,
        }
      },
    };
  },
  methods: {
    getAnimeData () {
      this.loading.anime = true;

      const params = {
        page: this.anime.pagination.current,
      }

      if (this.is_authenticated) {
        params['access_token'] = this.$store.state.auth.access_token
      }
      if (this.filter.data.name.length) {
        params['filter_name'] = this.filter.data.name
      }
      if (this.filter.data.year.length) {
        params['filter_year'] = this.filter.data.year
      }
      if (this.filter.data.season.length) {
        params['filter_season'] = this.filter.data.season
      }

      this.$http
        .get(process.env.VUE_APP_BACKEND_URL + "api/anime/index/", {
          params,
        })
        .then((response) => {
          // console.log(response);
          this.anime.data = response.data.media
          this.anime.pagination = response.data.pagination;

          this.loading.anime = false;
        }, (error) => {
          console.error(error);
        });
    },
    updateAnimeData (params = {}) {
      params['access_token'] = this.$store.state.auth.access_token

      this.$http
        .post(process.env.VUE_APP_BACKEND_URL + "api/anime/update/", params)
        .then((response) => {
          console.log(response);

          var data_index = this.anime.data.findIndex((media) => { return media.id == response.data.media_id })

          this.anime.data[data_index].progression = response.data.progression

          this.anime.data[data_index].progression_status = response.data.progression.status

          this.anime.data[data_index].action.can_complete = response.data.action.can_complete
          this.anime.data[data_index].action.can_watch = response.data.action.can_watch
          this.anime.data[data_index].action.can_planning = response.data.action.can_planning
          this.anime.data[data_index].action.can_rewatch = response.data.action.can_rewatch
          this.anime.data[data_index].action.can_add = response.data.action.can_add
        }, (error) => {
          console.error(error.response);
        });
    },
    deleteAnimeData (params = {}) {
      params['access_token'] = this.$store.state.auth.access_token

      this.$http
        .post(process.env.VUE_APP_BACKEND_URL + "api/anime/delete/", params)
        .then((response) => {
          console.log(response);

          var data_index = this.anime.data.findIndex((media) => { return media.id == response.data.media_id })

          this.anime.data[data_index].progression = null

          this.anime.data[data_index].progression_status = 'NONE'

          this.anime.data[data_index].action.can_complete = response.data.action.can_complete
          this.anime.data[data_index].action.can_watch = response.data.action.can_watch
          this.anime.data[data_index].action.can_planning = response.data.action.can_planning
          this.anime.data[data_index].action.can_rewatch = response.data.action.can_rewatch
          this.anime.data[data_index].action.can_add = response.data.action.can_add
        }, (error) => {
          console.error(error.response);
        });
    },
    toggleFilterDrawer () {
      this.filter.drawer.visible = !this.filter.drawer.visible
    },
    clearFilter () {
      // eslint-disable-next-line no-unused-vars
      for (const [key, value] of Object.entries(this.filter.data)) {
        this.filter.data[key] = ''
      }

      this.getAnimeData()
    },
    test () {
      console.log('TESTING START');

      const filter_param = []
      console.log(filter_param);
      // history.replaceState(null, '', this.$route.path + '')

      console.log('TESTING END');
    },
  },
  mounted () {
    this.getAnimeData()
  },
};
</script>

<style>
.anime-table-row > td {
  max-height: 3em;
  padding: 0.2rem !important;
}

.anime-table-title {
  font-family: "CWTEX-BB";
}

.anime-table-cover-image {
  max-width: 60px;
  max-height: 50px;
  min-height: 50px;
}

.anime-block-cover-box {
  width: 195px;
  height: 275px;
  overflow: hidden;
}

.anime-action-completed > span,
.anime-action-current > span,
.anime-action-add > span,
.anime-action-planning > span {
  margin: 0 0.2rem;
  font-size: 2rem;
  color: #1890ff;
}

.anime-action-delete > span {
  margin: 0 0.2rem;
  font-size: 2rem;
  color: #ff1865;
}
</style>